<template>
  <div class="center">
    <div class="center_breadcrumb">
      <crumb style="padding: 0 5px;">
        <div class="new--btn" slot="right" @click="openAdd">+ 新建</div>
        <div slot="right" @click="successdata" icon="el-icon-plus" style="float: left; margin-right: 10px; color: #03B187; cursor: pointer;"> <SvgIcon icon-class="icondaoru" size="18px" style="float: left;margin: 32px 5px 0 0"></SvgIcon> 导入</div>
      </crumb>
    </div>
    <div class="center_content">
      <!-- <div class="center_content_title">
        <SvgIcon icon-class="icondatixitongguanli" size="20px"></SvgIcon>
        <span>{{pageTitle}}</span>
      </div> -->
      <div class="center_content_search">
        <div class="center_content_search_left" style="width:100%">
          <div class="search-input-suffix gap">
            <label>学年：</label>
            <el-select v-model="page.grade" placeholder="请选择">
              <el-option
                v-for="item in gradeOptions"
                :key="item.id"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </div>
          <div class="search-input-suffix gap">
            <label>班级：</label>
            <el-select v-model="page.classId" filterable placeholder="请选择">
              <el-option
                v-for="item in courseClassification"
                :key="item.id"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </div>
          <!-- <div class="search-input-suffix gap">
                    <label>课程类型：</label>
                    <el-select v-model="page.courseProperty" placeholder="请选择">
                        <el-option
                        v-for="item in fieldCoursePropertyOptions"
                        :key="item.code"
                        :label="item.value"
                        :value="item.code">
                        </el-option>
                    </el-select>
          </div>-->
          <div class="search-input-suffix">
            <label>课程名称：</label>
            <el-input
              class="search"
              @keyup.enter.native="search"
              v-model="page.keywords"
              placeholder="请输入课程名称"
              clearable
            >
            </el-input>
            <!-- <el-button @click="search" type="primary">搜索</el-button> -->
          </div>
          <div class="search-input-suffix" style="margin: 0 0 0 20px">
            <div class="search--btn" @click="search">搜索</div>
          </div>
        </div>
        <!-- <div class="center_content_search_right">
          <p
            style="float: left;margin-right: 20px;cursor: pointer;margin-top: 3px;"
            @click="successdata"
          >
            <img src="@/assets/dao.png" alt style="float: left;margin-right: 5px;margin-top: 4px;" />
            导入
          </p>
          <el-button @click="openAdd" type="primary" icon="el-icon-plus" round>新建</el-button>
        </div> -->
      </div>
      <div class="table-container">
        <el-table :data="tableData" stripe style="width: 100%">
          <el-table-column prop="courseCode" label="课程代码"></el-table-column>
          <el-table-column prop="course" label="课程名称"></el-table-column>
          <el-table-column prop="classification" label="课程类型"></el-table-column>
          <el-table-column label="上课学年">
            <template slot-scope="scope">
              <div>{{scope.row.grade + '-' + (scope.row.grade + 1) + '学年'}}</div>
            </template>
          </el-table-column>
          <el-table-column prop="workNumber" label="教职工号"></el-table-column>
          <el-table-column prop="teacher" label="教师姓名"></el-table-column>
          <el-table-column prop="className" label="班级名称"></el-table-column>

          <!-- <el-table-column
                prop="classification"
                label="教职工号">
          </el-table-column>-->
          <!-- <el-table-column
                prop="className"
                label="班级"
                align="center">
          </el-table-column>-->

          <el-table-column label="操作" width="140">
                <template slot-scope="scope">                  
                    <div class="operate-button distance">
                        <SvgIcon icon-class="iconbianji1"></SvgIcon> 
                        <el-button
                        @click.native.prevent="editRow(scope.row)"
                        type="text"
                        size="small">
                        编辑
                        </el-button>
                    </div>
                    <div class="operate-button">
                        <SvgIcon icon-class="iconshanchu"></SvgIcon>
                        <el-button
                        @click.native.prevent="deleteRow(scope.row)"
                        type="text"
                        size="small"
                        style="color: #ff5858">
                        删除
                        </el-button>
                    </div>                    
                </template>
          </el-table-column>
        </el-table>
        <div class="center_pagination">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            background
            :current-page="page.page"
            :page-sizes="[15,30,40,50]"
            :page-size="page.rows"
            layout="prev, pager, next, jumper, sizes, total"
            :total="data.total"
          ></el-pagination>
        </div>
      </div>
    </div>
    <!-- 新增编辑 -->
    <el-dialog
      :title="dialogTitle"
      :visible.sync="userDialogFormVisible"
      :before-close="handleDialogClose"
      class="set-el-dialog"
      :close-on-click-modal="false"
    >
      <el-form :model="form" :rules="rules" ref="form" label-width="120px">
        <el-form-item label="班级" prop="classId">
          <el-select v-model="form.classId" @change="classChange" filterable placeholder="请选择班级">
            <el-option
              v-for="item in classOptions"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="课程" prop="courseId">
          <el-select v-model="form.courseId" filterable placeholder="请选择课程">
            <el-option
              v-for="item in courseOptions"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="教师" prop="teacherId">
          <el-select v-model="form.teacherId" filterable placeholder="请选择教师">
            <el-option
              v-for="item in teacherOptions"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="学年" prop="grade">
          <!-- <el-date-picker
                    v-model="yearVal"
                    @change="selectYear"
                    align="center"
                    type="year"
                    placeholder="请选择学年">
          </el-date-picker>-->
          <el-select v-model="form.grade" placeholder="请选择学年" @change="selectYear">
            <el-option v-for="item in options2" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="开课学期" prop="term">
          <el-radio-group v-model="form.term">
            <el-radio :label="1">第一学期</el-radio>
            <el-radio :label="2">第二学期</el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel('form')">取 消</el-button>
        <el-button type="primary" @click="create('form')" :disabled="isSubmit">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 弹框组件  导入 -->
    <form :model="form" enctype="Content-Type: multipart/form-data">
      <el-dialog
        title="导入排课管理"
        :visible.sync="dialogFormVisible1"
        :show-close="true"
        :close-on-click-modal="false"
      >
        <div style="height: 350px;" class="steps-dv1">
          <el-steps :active="index" direction="vertical">
            <el-step title="上传文件" icon="el-icon-edit">
              <template slot="description">
                <div>
                  导入排课管理之前，请先“
                  <el-tooltip class="item" effect="dark" content="请先下载模板" placement="top-start">
                    <span @click="xiazai" class="span-dv">下载模板</span>
                  </el-tooltip>”，按照模板格式录入数据。
                </div>
              </template>
            </el-step>
            <el-step title="检查数据" icon="el-icon-upload" description="等待文件上传完毕后自动检查表格数据。">
              <template slot="description">
                <div>等待文件上传完毕后自动检查表格数据。</div>
              </template>
            </el-step>
            <el-step title="完成" icon="el-icon-picture"></el-step>
          </el-steps>
          <!-- <el-button @click="dialogFormVisible = false" type="primary" class="steps-btn">上传文件</el-button> -->
          <el-upload
            class="upload-demo steps-btn"
            ref="upload"
            :action="uploadUrl()"
            accept=".xls"
            :on-remove="handleRemove"
            :file-list="fileList"
            :auto-upload="true"
            :on-success="handleSuccess"
            :on-error="handleError"
            :before-remove="beforeRemove"
            :limit="1"
            :on-exceed="handleExceed"
          >
            <el-button slot="trigger" size="small" type="primary">上传</el-button>
            <div slot="tip" class="el-upload__tip">支持文件格式：.xls 类型的文件，且不超过20M</div>
          </el-upload>
        </div>
        <div slot="footer" class="dialog-footer">
          <el-button @click="closciick">取 消</el-button>
          <el-button type="primary" @click="submitUpload ">确 定</el-button>
        </div>
      </el-dialog>
    </form>
  </div>
</template>

<script>
import trainingProgramApi from "@/api/trainingProgramApi";
import { formatDate } from "@/utils/utils";
export default {
  name: "CourseManagement",
  data() {
    return {
      dialogFormVisible1: false,
      form: {
        file: ""
      },
      file: "",
      index: 1,
      fileList: [],
      options2: [],
      pageTitle: "排课管理",
      data: {},
      isAdd: true,
      isSubmit:false,
      tableData: [],
      userDialogFormVisible: false,
      courseClassification: [],
      fieldCoursePropertyOptions: [],
      gradeOptions: [],
      classOptions: [],
      courseOptions: [],
      teacherOptions: [],
      // yearVal:'',
      form: {
        classId: "",
        courseId: "",
        teacherId: "",
        grade: "",
        term: 0,
        id: undefined
      },
      page: {
        grade: "",
        classId: "",
        courseProperty: null,
        keywords: "",
        page: 1,
        rows: 15
      },
      rules: {
        classId: [
          {
            required: true,
            message: "请选择班级",
            trigger: "blur"
          }
        ],
        courseId: [
          {
            required: true,
            message: "请选择课程",
            trigger: "blur"
          }
        ],
        teacherId: [
          {
            required: true,
            message: "请选择教师",
            trigger: "blur"
          }
        ],
        grade: [
          {
            required: true,
            message: "请选择学年",
            trigger: "blur"
          }
        ]
      }
    };
  },
  created() {},
  mounted() {
    this.initFn();
  },
  computed: {
    dialogTitle() {
      return this.isAdd ? "添加排课" : "编辑排课";
    }
  },
  methods: {
    //   导入
    successdata(index, row) {
      this.dialogFormVisible1 = true;
      this.teacherssId = row;
    },
    uploadUrl() {
      let token = localStorage.getItem("x_token");
      return `${this.API_URL}/basic/import/courseScheduling?x-token=${token}`;
    },
    xiazai() {
      location.href = this.API_URL + "/basic/import/template/courseScheduling";
    },
    submitUpload() {
      if (this.index === 3) {
        this.dialogFormVisible1 = false;
        this.index = 1;
      }
    },
    handleRemove(file, fileList) {
      this.form.file = "";
    },
    handleExceed(files, fileList) {
      this.$message.warning(
        `当前限制选择 1 个文件，本次选择了 ${
          files.length
        } 个文件，共选择了 ${files.length + fileList.length} 个文件`
      );
      if (files.size > 20480) {
        this.$message.warning(`sorry 上传文件大于20M,请重新上`);
      }
    },
    closciick(file) {
      if (this.index === 2) {
        this.index = 1;
      }
      this.dialogFormVisible1 = false;
    },
    // 文件上传成功时的钩子
    handleSuccess(res, file, fileList) {
      if (res.status === 1) {
        this.$notify.success({
          title: "success",
          message: res.message
        });
        this.index = 3;
      } else {
        if (res.data == null) {
          this.$notify.warning({
            title: "warning",
            message: res.message
          });
        } else {
          this.$notify.warning({
            title: "warning",
            message: res.data[0].message
          });
        }
        this.index = 2;
        this.dialogFormVisible1 = true;
      }
    },
    // 文件上传失败时的钩子
    handleError(err, file, fileList) {
      this.$notify.error({
        title: "错误",
        message: `文件上传失败`
      });
    },
    beforeRemove(file, fileList) {
      if (this.index === 3) {
        this.index = 1;
      } else if (this.index === 2) {
        this.index = 1;
      }
      this.$confirm(`确定移除 ${file.name}？`);
    },
    initFn() {
      this.initData();
      this.schedulingGrades();
      this.courseClassificationAll();
      this.fieldCourseProperty();
      //新增
      this.classAll();
      this.teacherAll();
    },
    classChange() {
      //  let specialtyId=''
      //  this.classOptions.forEach(vo =>{
      //          if(vo.id === this.form.classId){
      //              specialtyId=vo.specialtyId
      //          }
      //      })
      this.courseAll();
    },
    initData() {
      const url = trainingProgramApi.schedulingList;
      this.$post(url, this.page).then(res => {
        if (res.status === 1) {
          this.data = res;
          this.tableData = res.data;
        } else {
          this.$notify.error({
            title: "error",
            duration: 1500,
            message: res.message
          });
        }
      });
    },
    //所有学年
    schedulingGrades() {
      //  const url =trainingProgramApi.schedulingGrades;
      this.$get("basic/version/grades?num=10").then(res => {
        if (res.status === 1) {
          const allArr = {
            id: "",
            name: "全部"
          };
          this.options2 = JSON.parse(JSON.stringify(res.data));
          res.data.unshift(allArr);
          this.gradeOptions = res.data;
        } else {
          this.$notify.error({
            title: "error",
            duration: 1500,
            message: res.message
          });
        }
      });
    },
    //课程分类
    courseClassificationAll() {
      const url = trainingProgramApi.classAll;
      this.$get(url).then(res => {
        if (res.status === 1) {
          const allArr = {
            id: "",
            name: "全部"
          };
          res.data.unshift(allArr);
          this.courseClassification = res.data;
        } else {
          this.$notify.error({
            title: "error",
            duration: 1500,
            message: res.message
          });
        }
      });
    },
    //课程性质（课程类型）
    fieldCourseProperty() {
      const url = trainingProgramApi.fieldCourseProperty;
      this.$get(url).then(res => {
        if (res.status === 1) {
          const allArr = {
            code: null,
            value: "全部"
          };
          res.data.unshift(allArr);
          this.fieldCoursePropertyOptions = res.data;
        } else {
          this.$notify.error({
            title: "error",
            duration: 1500,
            message: res.message
          });
        }
      });
    },
    //所有班级
    classAll() {
      const url = trainingProgramApi.classAll;
      this.$get(url).then(res => {
        if (res.status === 1) {
          this.classOptions = res.data;
        } else {
          this.$notify.error({
            title: "error",
            duration: 1500,
            message: res.message
          });
        }
      });
    },
    //所有课程
    courseAll() {
      const url = trainingProgramApi.allByClass({
        specialtyId: this.form.classId
      });
      this.$get(url).then(res => {
        if (res.status === 1) {
          this.courseOptions = res.data;
        } else {
          this.$notify.error({
            title: "error",
            duration: 1500,
            message: res.message
          });
        }
      });
    },
    //所有教师
    teacherAll() {
      const url = trainingProgramApi.realall;
      this.$get(url).then(res => {
        if (res.status === 1) {
          this.teacherOptions = res.data;
        } else {
          this.$notify.error({
            title: "error",
            duration: 1500,
            message: res.message
          });
        }
      });
    },
    headInfo(item) {
      this.pageTitle = item[1];
    },
    search() {
      this.resetTemp();
      this.initData();
    },
    handleSizeChange(val) {
      this.page.rows = val;
      this.initData();
    },
    handleCurrentChange(obj) {
      this.page.page = obj;
      this.initData();
    },
    selectYear() {
      //    this.form.grade=formatDate(this.yearVal,'yyyy')
      
    },
    openAdd() {
      this.userDialogFormVisible = true;
      this.isAdd = true;
      this.isSubmit=false;
      this.$nextTick(() => {
         this.form = {
          classId: "",
          courseId: "",
          teacherId: "",
          grade: "",
          term: 0,
          id: undefined
        };
        this.$refs["form"].resetFields();
      });
    },
    deleteRow(row) {
      this.$confirm("确定删除该排课内容?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.userDel(row.id);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    editRow(row) {
      const obj = {
        classId: row.classId,
        courseId: row.courseId,
        teacherId: row.teacherId,
        grade: row.grade,
        term: row.term,
        id: row.id
      };
      // this.yearVal=new Date(JSON.stringify(row.grade));
      this.form = obj;
      this.classChange();
      this.userDialogFormVisible = true;
      this.isSubmit=false;
      this.isAdd = false;
    },
    create(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.isSubmit=true;
          if (this.isAdd) {
            this.userAdd();
          } else {
            this.userEdit();
          }
        }
      });
    },
    cancel(formName) {
      this.userDialogFormVisible = false;
      this.$refs[formName].resetFields();
    },
    handleDialogClose() {
      this.$refs["form"].resetFields();
      this.userDialogFormVisible = false;
    },
    userAdd() {
      const url = trainingProgramApi.schedulingAdd;
      this.$post(url, this.form)
        .then(res => {
          if (res.status === 1) { 
            this.isSubmit=true
            this.$notify.success({
              title: "success",
              duration: 1500,
              message: res.message
            });
            this.userDialogFormVisible = false;
            this.resetTemp();
            this.initData();
          } else {
            this.isSubmit=false
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
           this.isSubmit=false
          console.log(err);
        });
    },
    userDel(id) {
      const url = trainingProgramApi.schedulingDel({
        id: id
      });
      this.$deletes(url)
        .then(res => {
          if (res.status === 1) {
            this.$notify.success({
              title: "success",
              duration: 1500,
              message: res.message
            });
            this.initData();
          } else {
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    userEdit() {
      const url = trainingProgramApi.schedulingEdit;
      this.$put(url, this.form)
        .then(res => {
          if (res.status === 1) {
             this.isSubmit=true
            this.$notify.success({
              title: "success",
              duration: 1500,
              message: res.message
            });
            this.userDialogFormVisible = false;
            this.initData();
          } else {
             this.isSubmit=false
            this.$notify.error({
              title: "error",
              duration: 1500,
              message: res.message
            });
          }
        })
        .catch(err => {
           this.isSubmit=false
          console.log(err);
        });
    },
    resetTemp() {
      this.page.page = 1;
      this.page.rows = 15;
    }
  }
};
</script>

<style lang='scss'>
@import "../../styles/center.scss";
.steps-dv1 {
  position: relative;

  .steps-btn {
    position: absolute;
    top: 66px;
    left: 54px;
    .el-button--small,
    .el-button--small.is-round {
      margin-left: 100px;
    }
  }
}
.span-dv {
  cursor: pointer;
  color: #000;
  padding: 0 8px;
}
</style>




